#textEducation{
    padding-top: 60px;
    padding-bottom: 50px;
    color:#444649;
    font-weight: 600;
    text-align: center;
}
.straightLine{
    content: ' ';
    height: 100%;
    max-width: 6px;
    margin: 0 auto;
    background-color: #444649;
}
#school,#college{
    margin-bottom: 1rem !important;
    margin-top: 1rem !important;
}
.circleTimeOuter,.circleTimeInner{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 25%;
    transform: translate(-50%,-50%);
    background-color: red;
}
.circleTimeInner{
    width: 15px;
    height: 15px;
    background-color: white;
}