#textContact{
    padding-top: 60px;
    padding-bottom: 50px;
    color:#fff;
    font-weight: 600;
    text-align: center;
}
#Contact{
    background-color: #252934;
}
.inputControl{
    border: none !important;
    border-radius: 0 !important;
    background-color: #1e242c !important;
    color: white !important;
    
}
.inputControl::placeholder{
    color:#777;
}
#submitContact{
    border-radius: 0 !important;
    background-color: transparent;
    border: 2px solid white;
    padding: 10px 30px;
    color: white;
    font-weight: bold;
    float: right;
    transition: 0.3s ease;
}
#submitContact:hover{
    border-color: #04c2c9;
    background-color: #04c2c9;
}
#msgStatus{
    color: white;
    font-weight: 300;
}